@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,600&family=PT+Sans+Caption:wght@700&family=Roboto&display=swap');

$pricelee-blue: #8590e5;
$cheese-yellow: #efbc3e;
$dark-purple: #221126;
$lavender-blue: #8377dc;
$light-eggplant: #7c377c;

$link-visited: lighten($dark-purple, 15%);
$link-hover: lighten($dark-purple, 25%);
$link-active: lighten($cheese-yellow, 50%);
$deal-link: #ffffff;
$deal-link-visited: lighten($light-eggplant, 40%);
$deal-link-hover: lighten($light-eggplant, 50%);
$error-red: rgba(255, 0, 0, .7);
$success-green: rgba(0, 255, 0, .8);

$main-font: 'Roboto', sans-serif;
$heading-font: 'PT Sans Caption', sans-serif;
$logo-font: 'Montserrat', sans-serif;

